.row{
    margin-left:20px;
    color:#fff;
}
.posters{
    display:flex;
    padding:20px;
    overflow-x:scroll;
    overflow-y:hidden;
}
.posters::-webkit-scrollbar{
    display:none;
}
.poster{
    max-height:200px;
    margin-right:10px;
    cursor:pointer;
}
.smallPoster{
    max-height:100px;    
    margin-right:5px;
    cursor:pointer;
}
.poster:hover{
    transform:scale(1.1);
}
.smallPoster:hover{
    transform:scale(1.1);
}